import React, { Suspense } from 'react';
import { Routes, Route } from 'react-router-dom';
import { ROUTES } from './constants/routes';

import './styles/globals.scss';

import AppLayout from './components/layout/AppLayout';
import ErrorBoundaryWrapper from './components/ErrorBoundary/ErrorBoundaryWrapper'
import Spinner from './components/Spinner/Spinner';

//  
import FormattedTextDisplay from './components/FormattedTextDisplay/FormattedTextDisplay.jsx';
//  

const AboutPage = React.lazy(() => import('./pages/AboutPage/AboutPage'));
const HomePage = React.lazy(() => import('./pages/HomePage/HomePage'));
const TrainPage = React.lazy(() => import('./pages/TrainPage/TrainPage'));
const AddWordPage = React.lazy(() => import('./pages/AddWordPage/AddWordPage'));
const VocabPage = React.lazy(() => import('./pages/VocabPage/VocabPage'));
const NotFoundPage = React.lazy(() => import('./pages/NotFoundPage/NotFoundPage'));

function App() {
  return (
    <AppLayout>
      {/*  */}
      <FormattedTextDisplay />
      {/*  */}
      <Suspense fallback={<Spinner />}>
        <Routes>
          <Route path={ROUTES.ABOUT} element={<AboutPage />} />

          <Route
            path={ROUTES.HOME}
            element={
              <ErrorBoundaryWrapper
                title="   "
                message="     ."
                linkPath="/home"
                buttonText=""
              >
                <HomePage />
              </ErrorBoundaryWrapper>
            }
          />

          <Route
            path={ROUTES.TRAIN}
            element={
              <ErrorBoundaryWrapper
                title="   "
                message="   TrainPage. ,   ."
                linkPath="/train"
                buttonText=""
              >
                <TrainPage />
              </ErrorBoundaryWrapper>
            }
          />

          <Route
            path={ROUTES.ADD_WORD}
            element={
              <ErrorBoundaryWrapper
                title="     "
                message="      AddWordPage."
                linkPath="/add"
                buttonText=""
              >
                <AddWordPage />
              </ErrorBoundaryWrapper>
            }
          />        
  
          <Route
            path={ROUTES.VOCAB}
            element={
              <ErrorBoundaryWrapper
                title="     "
                message="VocabPage   .   ."
                linkPath="/vocab"
                buttonText=""
              >
                <VocabPage />
              </ErrorBoundaryWrapper>
            }
          />

          <Route path={ROUTES.NOT_FOUND} element={<NotFoundPage />} />
        </Routes>
      </Suspense>
    </AppLayout>
  );
}

export default App;
